.portal-container {
  font-family: Poppins;
  text-align: center;
  width: 90%;
  max-width: 800px;
  margin: 0 auto;
}

main {
  background-color: #faf8f5;
}
.portal-container div {
  text-align: center;
}
.portal-container input {
  border: 1px solid rgba(0, 0, 0, 0.521);
  width: 100%;
  border-radius: 2px;
  margin: 1rem 0;
  height: 2rem;
  padding-left: 1rem;
  background-color: transparent;
}

.btn-create {
  font-size: 1.1rem !important;
  border: 1px solid black !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 0 !important;
  position: relative;
  background-color: transparent !important;
  color: #171717 !important;
  margin-top: 0.8rem;
  width: 350px;
  margin: 1rem 0;
  box-shadow: 0;
}

.btn-create:focus {
  box-shadow: 0;
}

.btn-create-red {
  font-size: 1.1rem !important;
  border: 1px solid black !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 0 !important;
  position: relative;
  background-color: rgb(235, 64, 64) !important;
  color: #171717 !important;
  margin-top: 0.8rem;
  margin: 1rem 0;
  box-shadow: 0;
}

.btn-create-green {
  font-size: 1.1rem !important;
  border: 1px solid black !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 0 !important;
  position: relative;
  background-color: rgb(93, 182, 130) !important;
  color: #171717 !important;
  margin-top: 0.8rem;
  margin: 1rem 0;
  box-shadow: 0;
}

.btn-create-fit {
  font-size: 1.1rem !important;
  border: 1px solid black !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 0 !important;
  position: relative;
  background-color: transparent !important;
  color: #171717 !important;
  margin-top: 0.8rem;
  margin: 1rem 0;
  box-shadow: 0;
}
.btn-create-map {
  font-size: 1.1rem !important;
  border: 1px solid black !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 0 !important;
  position: relative;
  background-color: transparent !important;
  color: #171717 !important;
  margin-top: 1rem;
  box-shadow: 0;
  padding: 0.2rem 0 !important;
  width: 132px;
}
.btn-confirm-address {
  font-size: 0.7rem !important;
  border: 1px solid black !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 0 !important;
  position: relative;
  background-color: transparent !important;
  color: #171717 !important;
  box-shadow: 0;
  margin-top: 2.7rem;
}
.btn-confirmed-address {
  font-size: 0.7rem !important;
  border: 1px solid black !important;
  color: rgb(8, 8, 8) !important;
  border-radius: 0 !important;
  position: relative;
  background-color: rgb(83, 245, 78) !important;
  box-shadow: 0;
  margin-top: 2.7rem;
}
.btn-confirmed-address-filter {
  font-size: 0.9rem !important;
  border: 1px solid black !important;
  border-radius: 0 !important;
  position: relative;
  background-color: rgb(83, 245, 78) !important;
  color: #171717 !important;
  box-shadow: 0;
  margin-bottom: 1rem;
}
.btn-confirm-address-filter {
  font-size: 0.9rem !important;
  border: 1px solid black !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 0 !important;
  position: relative;
  background-color: transparent !important;
  color: #171717 !important;
  box-shadow: 0;

  margin-bottom: 1rem;
}
.form-label {
  color: #5c5c5c;
}
.map-container {
  justify-content: center;
  padding: 0;
  margin-top: 1.5rem;
  margin-bottom: 0;
  display: none;
}

.sandwich {
  text-decoration: none;
  font-size: 12px;
  color: #9e5627;
}
.column-portal {
  padding: 0 !important;
}
.column-portal button {
  font-size: 0.9rem !important;
}
.portal-buttons {
  max-width: 422px;
  /* margin: 0 auto !important; */
}
.col-portal-buttons {
  padding: 0 12px;
  font-family: Poppins;
}
.col-portal-left {
  text-align: left !important;
}
.col-portal-right {
  text-align: right !important;
}

@media (min-width: 990px) {
  .home-buttons {
    opacity: 0 !important;
  }
}

@media (min-width: 445px) {
  .portal-buttons {
    margin: 0 auto !important;
  }
}
